import React, { useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Select,
  Input,
  Switch,
  Radio,
  Checkbox,
  Breadcrumb,
} from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import styles from "./managePoll.module.scss";
import OrgSidebar from "../../containers/OrgSidebar";
import FloatLabel from "../form/FloatLabels";
import UploadImage from "./UploadImage";
import ReactStars from "react-rating-stars-component";
import MultiChoice from "./MultipleChoice";

const starsRating = {
  size: 40,
  count: 5,
  isHalf: false,
  value: 0,
  color: "#B4B4B4",
  activeColor: "#EA5042",
  onChange: (newValue) => {
    console.log(`Example 3: new value is ${newValue}`);
  },
};

function onChange(checked) {
  console.log(`switch to ${checked}`);
}

const { TextArea } = Input;
const { Content } = Layout;

const CreatePoll = () => {
  return (
    <React.Fragment>
      <React.Fragment>
        {" "}
        <Row className="inner-header">
          <LoggedinHeader amORGUser />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <OrgSidebar amORGUser />
              <Content>
              <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <NavLink to="/managepolls">
                      <Breadcrumb.Item>Manage Poll</Breadcrumb.Item>
                    </NavLink>
                    <Breadcrumb.Item>Create Poll</Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">Create Poll</h1>
                </Row>
                <div className="mid-content">
                  <Row>
                    <Col span={24}>
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                      >
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Poll Title">
                                <TextArea
                                  showCount
                                  maxLength={80}
                                  htmlFor="polltitle"
                                  className={`required ${styles.sm_textarea}`}
                                />
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Help Text">
                                <Form.Item
                                  className="required"
                                  name="help text"
                                  validateStatus="success"
                                >
                                  <Input
                                    className="input-control"
                                    id="helptext"
                                    htmlFor="helptext"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} className="gutter-row mt-3">
                              <h6>Upload</h6>
                              <Form.Item
                                name="help text"
                                className="upload-list"
                              >
                                <UploadImage />
                              </Form.Item>
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24} className="gutter-row">
                          <Row className="align-items-center d-flex">
                            <Col span={24} md={8} lg={8} xl={6}>
                              <Form.Item
                                name="showresult"
                                label="Show result to users "
                                className="mb-0"
                              >
                                <Switch onChange={onChange} />
                              </Form.Item>
                            </Col>
                            <Col span={24} lg={6}>
                              <FloatLabel label="Display final results as">
                                <Form.Item
                                  className="required mb-0"
                                  name="result"
                                >
                                  <>
                                    <Select
                                      className="input-control"
                                      htmlFor="result"
                                      className="required"
                                    >
                                      <Option value="1">Pie Chart</Option>
                                      <Option value="3">
                                        Horizontal Bar Chart
                                      </Option>
                                      <Option value="4">
                                        Vertical Bar Chart
                                      </Option>
                                    </Select>
                                  </>
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                      </Form>
                    </Col>
                  </Row>
                </div>

                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                      >
                        <Col span={24}>
                          <Row className="d-block">
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel label="Question">
                                <Form.Item
                                  className="required"
                                  name="question"
                                  validateStatus="success"
                                >
                                  <Input
                                    className="input-control"
                                    id="question"
                                    htmlFor="question"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={6} className="gutter-row">
                              <FloatLabel label="Question Type">
                                <Form.Item
                                  className="required"
                                  name="questiontype"
                                >
                                  <>
                                    <Select
                                      className=""
                                      htmlFor="questiontype"
                                      className="required"
                                    >
                                      <Option value="1">Single Choice</Option>
                                      <Option value="2">Multiple Choice</Option>
                                      <Option value="3">Star Rating</Option>
                                    </Select>
                                  </>
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} className="mt-4">
                              {/* <ReactStars {...starsRating} classNames="mt-3" /> */}
                              {/* <Radio>Single Choice</Radio> */}
                              <div className="d-flex flex-column flex-gap chk-opt w-25 w-md-100">
                                <span className="opts">
                                  <Checkbox onChange={onChange}>
                                    Option 1
                                  </Checkbox>
                                  <span className="icon icon-close font-12"></span>
                                </span>
                                
                              </div>

                              <Row>
                                <Col span={24} md={6} className="add-opt">
                                  <MultiChoice />
                                </Col>
                              </Row>
                            </Col>
                          </Row>
                        </Col>

                        <div className="mt-5">
                          <Button type="primary" className="md mr-4">
                            Preview
                          </Button>
                          <Button
                            type="secondary"
                            type="secondary"
                            htmlType="submit"
                          >
                            Cancel
                          </Button>
                        </div>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </React.Fragment>
    </React.Fragment>
  );
};

export default CreatePoll;
