import { LOGIN_SUCCESS, LOGIN_FAIL, LOGOUT } from "../actions/types";
import * as AppStorage from "../utils/AppStorage";

var CryptoJS = require("crypto-js");

let decryptedPassword = "";
const localPwd = AppStorage.get("pwd");
decryptedPassword = localPwd
  ? CryptoJS.AES.decrypt(localPwd, "anonymoose")
  : "";
const user = JSON.parse(AppStorage.get("user"));
const password = decryptedPassword.toString(CryptoJS.enc.Utf8);

const initialState = user
  ? { isLoggedIn: true, user, pwd: password.replaceAll('^"+|"+$', "") }
  : { isLoggedIn: false, user: null, pwd: null };

export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case LOGIN_SUCCESS:
      return {
        ...state,
        isLoggedIn: true,
        user: payload.user,
        pwd: payload.pwd,
      };
    case LOGIN_FAIL:
      return {
        ...state,
        isLoggedIn: false,
        user: null,
        pwd: null,
      };
    case LOGOUT:
      return {
        ...state,
        isLoggedIn: false,
        user: null,
        pwd: null,
      };
    default:
      return state;
  }
}
