//Authentication service for AnonyMoose
import axios from "axios";
import { BASE_URL } from "../userconfig";
import * as AppStorage from "../utils/AppStorage";

var CryptoJS = require("crypto-js");

const options = {
  headers: {
    Accept: "application/json, text/plain",
    "Content-Type": "application/json;charset=UTF-8",
  },
};

class AuthService {
  let;
  async login(email, password, page) {
    const response = await axios.post(
      BASE_URL + "authenticate/",
      { email, password, page },
      options
    );
    if (response.data) {
      AppStorage.set("user", JSON.stringify(response.data));
      AppStorage.set(
        "pwd",
        CryptoJS.AES.encrypt(JSON.stringify(password), "anonymoose").toString()
      );
    }
    return response.data;
  }

  logout() {
    AppStorage.removeAll();
    window.location.reload();
  }
}

export default new AuthService();
