import React from "react";
import Grid from "../table/Table";
import { Row, Col, Button } from "antd";
import GridPopover from "../table/Popover";

class AssignedUsers extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }

  componentDidMount() {
    let { seededUsers } = this.props;
    this.setState({ data: seededUsers });
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      this.props.seededUsers !== nextProps.seededUsers &&
      nextProps.seededUsers.length > 0
    ) {
      this.setState({ data: nextProps.seededUsers });
    }
  }

  render() {
    const columns = [
      {
        title: "First Name",
        dataIndex: "firstName",
        key: "firstName",
        width: "22%",
        render: (firstName) => {
          return {
            children: firstName,
            props: {
              "data-title": "First Name",
            },
          };
        },
      },
      {
        title: "Last Name",
        dataIndex: "lastName",
        key: "lastName",
        width: "22%",
        render: (lastName) => {
          return {
            children: lastName,
            props: {
              "data-title": "Last Name",
            },
          };
        },
      },
      {
        title: "Mobile",
        dataIndex: "mobile",
        key: "mobile",
        width: "22%",
        render: (mobile) => {
          return {
            children: mobile.includes("+") ? mobile : "+"+mobile,
            props: {
              "data-title": "Mobile",
            },
          };
        },
      },
      {
        title: "Email",
        dataIndex: "email",
        key: "email",
        width: "21%",
        render: (email) => {
          return {
            children: email,
            props: {
              "data-title": "Email",
            },
          };
        },
      },
      {
        title: "Status",
        dataIndex: "status2",
        key: "status2",
        width: "10%",
        render: (status2) => {
          return {
            children:
              status2 === 1 || status2 === "1" ? (
                <div className="primary-tag-rounded tag">
                  <span className="circle mr-1"></span>Active
                </div>
              ) : (
                <div className="secondary-tag-rounded tag">Inactive</div>
              ),
            props: {
              "data-title": "Status",
            },
          };
        },
      },
      {
        title: "",
        dataIndex: "",
        key: "x",
        width: "5%",
        render: (record) => {
          return (
            <GridPopover
              handleEditUser={this.props.handleEditUser}
              handleDelete={this.props.handleDelete}
              user={record}
              seededUsers={this.props.seededUsers}
              seededUser
              viaEdit={this.props.viaEdit}
            />
          );
        },
      },
    ];
    return (
      <Row className="content-center pt-0" data-test="addSeededuser">
        {this.props.fromStepTwo ? (
          <Col className="d-flex align-items-center justify-content-end">
            <Button
              className="mr-4"
              type="secondary"
              onClick={() =>
                this.props.fromStepTwo
                  ? this.props.showAddSeededUserForm()
                  : this.props.addSeededUser()
              }
            >
              Add Seeded User
            </Button>
          </Col>
        ) : null}
        <Grid
          rowKey="unique_identifier"
          columns={columns}
          data={this.state.data}
        />
      </Row>
    );
  }
}

export default AssignedUsers;
