import React, { useEffect, useState, useRef } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Input,
  Typography,
  Badge,
  Tag,
  Spin,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "./hotlineMessages.scss";
import AmWebSidebar from "./AmWebSidebar";
import FilterPopover from "./FilterPopover";
import axios from "axios";
import moment from "moment";
import authHeader from "../../services/auth-header";
import Messages from "./Messages";
import { BASE_URL } from "../../userconfig";
import { Scrollbars } from "react-custom-scrollbars";
import * as AppStorage from "../../utils/AppStorage";
import NoHotlines from "../../assets/images/no-hotlines.svg";
import CreateHotline from "../../assets/images/create-hotline.svg";
import { isMobile } from "mobile-device-detect";

import { LeftOutlined } from "@ant-design/icons";

const { Paragraph } = Typography;
const { Search } = Input;
const { Content } = Layout;

const HotlineMessages = () => {
  const [conversations, setConversations] = useState([]);
  const [initialConv, setInitialConv] = useState([]);
  const [loader, setLoader] = useState(true);
  const [urgencyFilterData, setUrgencyFilterData] = useState([]);
  const [hotlineFilterData, setHotlineFilterData] = useState([]);
  const [statusFilterData, setStatusFilterData] = useState([]);
  const [removeFilter, setRemoveFilter] = useState({});
  const [conversation, setConversation] = useState("");
  const [urgencyLevel, setUrgencyLevel] = useState("");
  const [status, setStatus] = useState("");
  const [searchString, setSearchString] = useState("");
  const [socket, setSocket] = useState(null);
  const [newState, setNewState] = useState({});
  const [unreadCount, setUnreadCount] = useState(0);
  const [allConversations, setAllConversations] = useState([]);
  const [showdiv, setShowDiv] = useState(false);
  const [chatdiv, setChatDiv] = useState(false);
  const webSocket = useRef(null);
  const [form] = Form.useForm();

  useEffect(() => {
    getConversations();
    connect();
    unSetFilters();
  }, []);

  // useEffect(() => {
  //   var element = document.getElementsByClassName("inbox-messages");
  //   if (element && element[0]) {
  //     element[0].scrollIntoView(false);
  //   }
  // }, [initialConv]);

  // CURRENTLY NOT IN USE.
  /* useEffect(() => {
     handleOverallCount();
   }, [conversations, unreadCount]); */

  useEffect(() => {
    let allConversationIds = [];
    let mergedConversations = [];
    allConversations?.map(({ conversation_id }) => {
      let convoID = "";
      // check if the conversation ID has a highlighted search text.
      if (conversation_id?.includes("</mark>")) {
        // remove the mark tag from the ID to get the plain raw value.
        convoID = conversation_id?.toString().replace(/(<([^>]+)>)/gi, "");
      } else {
        convoID = conversation_id;
      }
      allConversationIds.push(convoID);
    });
    if (!allConversationIds.includes(newState?.conversation_id)) {
      mergedConversations = [...allConversations, newState];
    } else {
      mergedConversations = allConversations;
    }
    setAllConversations(mergedConversations);
    increaseOverallCountDuringFilters(newState, allConversations);
  }, [newState, allConversations]);

  const connect = () => {
    webSocket.current = new WebSocket(
      "ws://10.0.6.10:8000/ws/hotlineMessages/"
    );
    webSocket.current.onopen = () => {
      setSocket(webSocket.current);
    };
    webSocket.current.onmessage = (event) => {
      console.log("WEBSOCKET Event Received!", event);
      let newConvo = JSON.parse(event.data);
      setNewState(newConvo);
      socketMethod(newConvo);
    };

    webSocket.current.onclose = () => {
      console.log("WEBSOCKET Closed!");
    };
    webSocket.current.onerror = (error) => {
      console.log("WEBSOCKET Error!", error);
    };
  };

  // return the updated conversations with new messages via socket feature.
  const socketMethod = (newConvo) => {
    const user = JSON.parse(AppStorage.get("user"));
    // only show conversations which are assigned to the user.
    if (newConvo.accessed_users.includes(user.user_data.user_id)) {
      setConversations((conversations) => {
        const urgencyState =
          AppStorage.get("urgencyLevels") &&
          AppStorage.get("urgencyLevels").split(",");
        const statusState =
          AppStorage.get("status") && AppStorage.get("status").split(",");
        const hotlineState =
          AppStorage.get("hotline") && AppStorage.get("hotline").split(",");
        const searchBy =
          AppStorage.get("searchBy") && AppStorage.get("searchBy").toString();
        // extracts IDs of all the present conversations for an AMWeb user.
        let conversationIds = [];
        conversations.map(({ conversation_id }) => {
          let convoID = "";
          // check if the conversation ID has a highlighted search text.
          if (conversation_id.includes("</mark>")) {
            // remove the mark tag from the ID to get the plain raw value.
            convoID = conversation_id.toString().replace(/(<([^>]+)>)/gi, "");
          } else {
            convoID = conversation_id;
          }
          conversationIds.push(convoID);
        });
        var data = {
          conversation_id: newConvo.conversation_id,
          user_id: user.user_details.org_user_id,
        };
        let updatedNewConvo = newConvo;
        let conversationsToDisplay = conversations;
        let urgencyFilter = false;
        let statusFilter = false;
        let hotlineFilter = false;
        let searchFilter = false;
        let hasConversationWithSearchedWord = [];
        // let hasMessagesWithSearchedWord = [];
        let convoWithSearchWord = [];
        let totalFiltersApplied = {
          urgency: "",
          status: "",
          hotline: "",
          searchBy: "",
        };
        // check if there is any filter applied on conversations.
        if (urgencyState !== null && urgencyState?.length > 0) {
          urgencyFilter = true;
          totalFiltersApplied = {
            ...totalFiltersApplied,
            urgency: urgencyState,
          };
        }
        if (statusState !== null && statusState?.length > 0) {
          statusFilter = true;
          totalFiltersApplied = {
            ...totalFiltersApplied,
            status: statusState,
          };
        }
        if (hotlineState !== null && hotlineState?.length > 0) {
          hotlineFilter = true;
          totalFiltersApplied = {
            ...totalFiltersApplied,
            hotline: hotlineState,
          };
        }
        if (searchBy !== null && searchBy?.length > 0) {
          searchFilter = true;
          totalFiltersApplied = {
            ...totalFiltersApplied,
            searchBy: searchBy,
          };
        }
        if (urgencyFilter || statusFilter || hotlineFilter || searchFilter) {
          conversationsToDisplay = filterSocket(
            totalFiltersApplied,
            newConvo,
            conversationsToDisplay,
            conversationIds
          );
        }

        if (searchFilter) {
          convoWithSearchWord = [
            newConvo.conversation_id,
            newConvo.responder_conversation_name,
            newConvo.hotline_name,
            newConvo.message,
          ];
          hasConversationWithSearchedWord = convoWithSearchWord.map((value) =>
            value.toLowerCase().includes(searchBy.toLowerCase())
          );
          /* let convoMessages = [];
          const token = authHeader();
          const options = {
            headers: {
              Authorization: token,
              Accept: "application/json, text/plain",
              "Content-Type": "application/json;charset=UTF-8",
            },
          };
          axios
            .post(BASE_URL + "hotlineConvoMessages/", data, options)
            .then(async (res) => {
              convoMessages = res.data.map((value) => value.message);
            });
          hasMessagesWithSearchedWord = convoMessages.map((value) =>
            value.toLowerCase().includes(searchBy.toLowerCase())
          ); */
        }
        if (
          !searchFilter &&
          !urgencyFilter &&
          !statusFilter &&
          !hotlineFilter
        ) {
          // check if a new conversation is initiated from a mobile App user which is not already present in AMWeb.
          // if yes, append that new conversation to the existing list of conversations.
          if (!conversationIds.includes(newConvo.conversation_id)) {
            conversationsToDisplay = [...conversationsToDisplay, newConvo];
          }
        }
        const updatedConversations = conversationsToDisplay.map((x) => {
          // check if the message has been sent in a conversation via different user other than logged in user.
          if (
            x.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
              newConvo.conversation_id &&
            newConvo.created_by !== user.user_data.user_id
          ) {
            // check if the new coming conversation includes some searched text.
            if (x.responder_conversation_name.includes("</mark>")) {
              updatedNewConvo = {
                ...updatedNewConvo,
                responder_conversation_name: x.responder_conversation_name,
              };
            }
            if (x.conversation_id.includes("</mark>")) {
              updatedNewConvo = {
                ...updatedNewConvo,
                conversation_id: x.conversation_id,
              };
            }
            if (x.hotline_name.includes("</mark>")) {
              updatedNewConvo = {
                ...updatedNewConvo,
                hotline: {
                  name: x.hotline_name,
                },
              };
            }
            // increase the unread message count of this new conversation.
            updatedNewConvo = {
              ...updatedNewConvo,
              unread_messages_count: x.unread_messages_count + 1,
            };
            return updatedNewConvo;
          }
          // check if the message has been sent in a conversation via same user as logged in user.
          else if (
            x.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
              newConvo.conversation_id &&
            newConvo.created_by === user.user_data.user_id
          ) {
            // check if the new incoming conversation includes some searched text.
            if (x.responder_conversation_name.includes("</mark>")) {
              updatedNewConvo = {
                ...updatedNewConvo,
                responder_conversation_name: x.responder_conversation_name,
              };
            }
            if (x.conversation_id.includes("</mark>")) {
              updatedNewConvo = {
                ...updatedNewConvo,
                conversation_id: x.conversation_id,
              };
            }
            if (x.hotline_name.includes("</mark>")) {
              updatedNewConvo = {
                ...updatedNewConvo,
                hotline: {
                  name: x.hotline_name,
                },
              };
            }
            return updatedNewConvo;
          }
          // return a list of the remaining conversations as it is other than the new coming conversation.
          else {
            return x;
          }
        });
        // increment the overall unread count as per the new incoming conversations when there is not filter applied.
        if (
          !urgencyFilter &&
          !statusFilter &&
          !hotlineFilter &&
          !searchFilter
        ) {
          increaseOverallCount(newConvo, conversationsToDisplay);
        }
        let finalConversations = [];
        if (
          urgencyFilter &&
          !urgencyState.includes(
            newConvo.urgency_level_id.toString()
          )
        ) {
          finalConversations = updatedConversations;
        } else if (
          statusFilter &&
          !statusState.includes(newConvo.status_id.toString())
        ) {
          finalConversations = updatedConversations;
        } else if (
          hotlineFilter &&
          !hotlineState.includes(newConvo.hotline_id.toString())
        ) {
          finalConversations = updatedConversations;
        } else if (
          searchFilter &&
          !hasConversationWithSearchedWord.includes(true)
        ) {
          finalConversations = updatedConversations;
        } else {
          finalConversations = displayNewConvAtTop(
            updatedConversations,
            newConvo,
            updatedNewConvo
          );
        }
        return finalConversations;
      });
    }
  };

  // append the new incoming conversation at the top of the existing list of conversations.
  const displayNewConvAtTop = (
    updatedConversations,
    newConvo,
    updatedNewConvo
  ) => {
    let index = 0;
    // create a replica of updatedConversations
    const dummyArray = updatedConversations;
    // return the index of the new incoming conversation from the list of all conversations.
    updatedConversations.map((value) => {
      if (
        value?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
        newConvo?.conversation_id
      ) {
        return (index = updatedConversations.indexOf(value));
      }
    });
    // remove that new conversation from the dummy list temporarily.
    dummyArray.splice(index, 1);
    // push that removed incoming conversation on top of the list.
    const finalConversations = [updatedNewConvo, ...dummyArray];
    return finalConversations;
  };

  // increase the overall unread count as per the new incoming conversations via socket without any search & filters.
  const increaseOverallCount = (newConvo, conversationsToDisplay) => {
    const user = JSON.parse(AppStorage.get("user"));
    // return only that new incoming message which comes from a user other than logged in user.
    const newConvFromDiffUser = conversationsToDisplay.filter(
      (filter) =>
        filter?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
          newConvo?.conversation_id &&
        newConvo.created_by !== user.user_data.user_id
    );
    // return the new incoming message irrespective of any user.
    const incomingConversation = conversationsToDisplay.filter(
      (ele) =>
        ele?.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
        newConvo?.conversation_id
    );
    for (var i = 0; i < newConvFromDiffUser.length; i++) {
      if (
        incomingConversation[0]?.conversation_id
          .toString()
          .replace(/(<([^>]+)>)/gi, "") ===
          newConvFromDiffUser[i]?.conversation_id
            .toString()
            .replace(/(<([^>]+)>)/gi, "") &&
        incomingConversation[0].unread_messages_count === 0
      ) {
        setUnreadCount((unreadCount) => unreadCount + 1);
      }
    }
  };

  // increase the overall unread count as per the new incoming conversations via socket in case of search & filters applied.
  const increaseOverallCountDuringFilters = (newState, allConversations) => {
    if (
      urgencyFilterData.length > 0 ||
      statusFilterData.length > 0 ||
      hotlineFilterData.length > 0 ||
      searchString.length > 0
    ) {
      const user = JSON.parse(AppStorage.get("user"));
      // return only that new incoming message which comes from a user other than logged in user.
      const newConvFromDiffUser = allConversations?.filter(
        (filter) =>
          filter?.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
            newState?.conversation_id &&
          newState.created_by !== user.user_data.user_id
      );
      // return the new incoming message irrespective of any user.
      const incomingConversation = allConversations?.filter(
        (ele) =>
          ele?.conversation_id?.toString().replace(/(<([^>]+)>)/gi, "") ===
          newState?.conversation_id
      );
      for (var i = 0; i < newConvFromDiffUser.length; i++) {
        if (
          incomingConversation[0]?.conversation_id
            ?.toString()
            .replace(/(<([^>]+)>)/gi, "") ===
            newConvFromDiffUser[i]?.conversation_id
              ?.toString()
              .replace(/(<([^>]+)>)/gi, "") &&
          incomingConversation[0]?.unread_messages_count === 0
        ) {
          setUnreadCount((unreadCount) => unreadCount + 1);
        }
      }
    }
  };

  // remove filters when component loads.
  const unSetFilters = () => {
    AppStorage.remove("urgencyLevels");
    AppStorage.remove("status");
    AppStorage.remove("hotline");
    AppStorage.remove("searchBy");
  };

  // return those conversations which have matched filters or search.
  const filterSocket = (
    totalFiltersApplied,
    newConvo,
    conversations,
    conversationIds
  ) => {
    let totalConversations = [];
    if (
      (totalFiltersApplied.status?.includes(
        newConvo.status_id.toString()
      ) &&
        totalFiltersApplied.urgency?.includes(
          newConvo.urgency_level_id.toString()
        ) &&
        totalFiltersApplied.hotline?.includes(
          newConvo.hotline_id.toString()
        ) &&
        totalFiltersApplied.searchBy?.length > 0 &&
        newConvo.conversation_id
          .toLowerCase()
          .includes(totalFiltersApplied.searchBy?.toLowerCase())) ||
      newConvo.responder_conversation_name
        .toLowerCase()
        .includes(totalFiltersApplied.searchBy?.toLowerCase()) ||
      newConvo.hotline_name
        .toLowerCase()
        .includes(totalFiltersApplied.searchBy?.toLowerCase()) ||
      newConvo.message
        .toLowerCase()
        .includes(totalFiltersApplied.searchBy?.toLowerCase())
    ) {
      let conversation_id = newConvo.conversation_id.replace(
        new RegExp(totalFiltersApplied.searchBy, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
      let responder_conversation_name = newConvo.responder_conversation_name.replace(
        new RegExp(totalFiltersApplied.searchBy, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
      let hotlineName = newConvo.hotline_name.replace(
        new RegExp(totalFiltersApplied.searchBy, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
      let itemMessage = newConvo.message.replace(
        new RegExp(totalFiltersApplied.searchBy, "gi"),
        (match) =>
          `<mark style="background: #2769AA; color: white;">${match}</mark>`
      );
      const updatedConversation = {
        ...newConvo,
        conversation_id: conversation_id,
        responder_conversation_name: responder_conversation_name,
        message: itemMessage,
        hotline: {
          name: hotlineName,
          hotline_id: newConvo.hotline_id,
        },
      };
      if (
        !conversationIds.includes(
          updatedConversation.conversation_id
            ?.toString()
            .replace(/(<([^>]+)>)/gi, "")
        )
      ) {
        totalConversations = [...conversations, updatedConversation];
      } else {
        totalConversations = conversations;
      }
    } else {
      totalConversations = conversations;
    }
    return totalConversations;
  };

  // set state values of applied filters.
  function setAppliedFilters(urgencyLevels, status, hotline) {
    setUrgencyFilterData(urgencyLevels);
    setStatusFilterData(status);
    setHotlineFilterData(hotline);
    AppStorage.set(
      "urgencyLevels",
      Object.values(urgencyLevels)?.map((data) => data.value)
    );
    AppStorage.set(
      "status",
      Object.values(status)?.map((data) => data.value)
    );
    AppStorage.set(
      "hotline",
      Object.values(hotline)?.map((data) => data.value)
    );
  }

  // return the filtered conversations according to the applied filters.
  const loadFilteredConversations = (
    data,
    type,
    selectedUrgency,
    selectedStatus,
    selectedHotline
  ) => {
    setAppliedFilters(selectedUrgency, selectedStatus, selectedHotline);
    setConversations(data);
    if (type === "reset") {
      form.setFieldsValue({ search: "" });
    }
  };

  // remove the applied filter after clicking on its corresponding remove icon.
  function removeFilterLevel(type, value) {
    setRemoveFilter({ type, value });
  }

  const getConversations = () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const urgencyState =
      AppStorage.get("urgencyLevels") &&
      AppStorage.get("urgencyLevels").split(",");
    const statusState =
      AppStorage.get("status") && AppStorage.get("status").split(",");
    const hotlineState =
      AppStorage.get("hotline") && AppStorage.get("hotline").split(",");
    let allAppliedFilters = {
      ...(urgencyState &&
        urgencyState.length > 0 && { urgency_level_id: urgencyState }),
      ...(statusState && statusState.length > 0 && { status_id: statusState }),
      ...(hotlineState &&
        hotlineState.length > 0 && { hotline_id: hotlineState }),
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      user.user_details.org_user_id;

    const urlWithFilters =
      searchString.length > 0 &&
      urgencyFilterData.length === 0 &&
      statusFilterData.length === 0 &&
      hotlineFilterData.length === 0
        ? baseUrl + "&search=" + searchString
        : searchString.length > 0 &&
          (urgencyFilterData.length > 0 ||
            statusFilterData.length > 0 ||
            hotlineFilterData.length > 0)
        ? baseUrl +
          "&filter=" +
          JSON.stringify(allAppliedFilters) +
          "&search=" +
          searchString
        : searchString.length === 0 &&
          (urgencyFilterData.length > 0 ||
            statusFilterData.length > 0 ||
            hotlineFilterData.length > 0)
        ? baseUrl + "&filter=" + JSON.stringify(allAppliedFilters)
        : "";
    if (user) {
      if (urlWithFilters.length > 0) {
        axios.get(urlWithFilters, options).then((res) => {
          setConversations(res.data);
        });
      } else {
        axios.get(baseUrl, options).then((res) => {
          setLoader(false);
          let disabledCount = 0;
          setInitialConv(res.data);
          setConversations(res.data);
          setAllConversations(res.data);
          const selectedUsers = res.data.reduce(
            (accumulator, currentValue, index) => {
              if (currentValue.unread_messages_count != 0) {
                disabledCount++;
              }
            },
            []
          );
          setUnreadCount(disabledCount);
        });
      }
    }
  };

  const onBackClick = () => {
    setShowDiv(false);
    setChatDiv(false);
  };

  const onConversationClick = (conversation) => {
    if (isMobile) {
      setShowDiv(true);
      setChatDiv(true);
    }
    let updatedData = {};
    setConversations((conversations) => {
      updatedData = conversations.map((x) =>
        x.conversation_id.toString().replace(/(<([^>]+)>)/gi, "") ===
        conversation.conversation_id.toString().replace(/(<([^>]+)>)/gi, "")
          ? { ...conversation, unread_messages_count: 0 }
          : x
      );
      return updatedData;
    });
    if (conversation.unread_messages_count > 0 && unreadCount > 0) {
      const leftCount = unreadCount - 1;
      setUnreadCount(leftCount);
    }
    setConversation(conversation);
    setUrgencyLevel(conversation.urgency_level_id);
    setStatus(conversation.status_id);
  };

  // set the value of an overall count on send message from any conversation.
  const setCounter = (conversation) => {
    let receivedConversation = conversation.data;
    for (var i = 0; i < conversations.length - 1; i++) {
      if (
        conversations[i].conversation_id
          .toString()
          .replace(/(<([^>]+)>)/gi, "") ===
        conversation.data.conversation_id
          .toString()
          .replace(/(<([^>]+)>)/gi, "")
      ) {
        receivedConversation = {
          ...receivedConversation,
          unread_messages_count: conversations[i].unread_messages_count,
        };
      }
    }
    if (
      receivedConversation.unread_messages_count &&
      receivedConversation.unread_messages_count > 0 &&
      unreadCount > 0
    ) {
      const leftCount = unreadCount - 1;
      setUnreadCount(leftCount);
    }
  };

  // CURRENTLY NOT IN USE.
  // set an overall count of unread conversations depending on the filters and search applied.
  /* const handleOverallCount = () => {
    const unreadConversations = conversations.filter(
      (value) => value.unread_count > 0
    );
    setUnreadCount(unreadConversations.length);
  }; */

  const onChangeUrgencyLevel = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      urgency_level_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesUrgencyLevel/ ", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setUrgencyLevel(event);
        }
      });
  };

  const onChangeStatus = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      status_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesStatus/", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setStatus(event);
        }
      });
  };

  const createMarkup = (html) => {
    return { __html: html };
  };

  // highlight the text found during search.
  const highlightText = (data, str) => {
    let highlightedObject = {};
    highlightedObject = data
      .filter(
        (item) =>
          item.conversation_id.toLowerCase().includes(str.toLowerCase()) ||
          item.responder_conversation_name
            .toLowerCase()
            .includes(str.toLowerCase()) ||
          item.hotline_name.toLowerCase().includes(str.toLowerCase()) ||
          item.message.toLowerCase().includes(str.toLowerCase())
      )
      .map((item) => {
        let conversation_id = item.conversation_id.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        let responder_conversation_name = item.responder_conversation_name.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        let hotlineName = item.hotline_name.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        let itemMessage = item.message.replace(
          new RegExp(str, "gi"),
          (match) =>
            `<mark style="background: #2769AA; color: white;">${match}</mark>`
        );
        return {
          ...item,
          conversation_id: conversation_id,
          responder_conversation_name: responder_conversation_name,
          message: itemMessage,
          hotline: {
            name: hotlineName,
            hotline_id: item.hotline_id,
          },
        };
      });
    return highlightedObject;
  };

  // return the filtered conversations on search filter.
  const onSearch = (e) => {
    var str = e.trim();
    const statusFil = statusFilterData.map((status) => {
      status = status.value;
      return status;
    });
    let allCheckedOnes = {
      ...(statusFil.length > 0 && { status_id: statusFil }),
      ...(urgencyFilterData.length > 0 && {
        urgency_level_id: urgencyFilterData.map((ugc) => ugc.value),
      }),
      ...(hotlineFilterData.length > 0 && {
        hotline_id: hotlineFilterData.map((ht) => ht.value),
      }),
    };
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      user.user_details.org_user_id;
    const finalUrl =
      str === ""
        ? baseUrl + "&filter=" + JSON.stringify(allCheckedOnes)
        : baseUrl +
          "&filter=" +
          JSON.stringify(allCheckedOnes) +
          "&search=" +
          str.toString();
    axios.get(finalUrl, options).then((res) => {
      setSearchString(str);
      AppStorage.set("searchBy", str);
      let filteredData = [];
      if (str.length > 0) {
        filteredData = highlightText(res.data, str);
      } else {
        filteredData = res.data;
      }
      setConversations(filteredData);
    });
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amWEBUser />
      </Row>
      <Row className="content-center pt-0" data-test="hotlinemsg">
        <Layout>
          <Layout>
            <AmWebSidebar amWEBUser />
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">
                  <span>
                    <LeftOutlined
                      className="d-inline-flex d-lg-none pr-3 back-arrow "
                      title="Back to Messages"
                      onClick={() => onBackClick()}
                    />
                  </span>
                  Hotline Messages
                </h1>
              </Row>
              {loader ? (
                <Col
                  span={24}
                  lg={18}
                  className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
                >
                  <Row className="justify-content-center my-3">
                    <Spin
                      size="large"
                      tip="Loading..."
                      spinning={loader}
                    ></Spin>
                  </Row>
                </Col>
              ) : !loader && initialConv?.length > 0 ? (
                <div className="mid-content p-0">
                  <Row>
                    <Col
                      span={24}
                      lg={6}
                      className={
                        showdiv
                          ? "message-section py-4 d-none"
                          : "message-section py-4"
                      }
                    >
                      <Col
                        span={24}
                        className=" d-flex justify-content-between px-3"
                      >
                        <h6>
                          Conversations{" "}
                          <Badge className="pl-1" count={unreadCount} />
                        </h6>

                        <FilterPopover
                          searchString={searchString}
                          removeFilter={removeFilter}
                          loadFilteredConversations={loadFilteredConversations}
                        />
                      </Col>

                      <Col span={24} className="mt-4 px-3 d-block">
                        <Scrollbars
                          autoHeight
                          autoHeightMin={0}
                          autoHeightMax={150}
                          thumbMinSize={30}
                          universal={true}
                        >
                          <>
                            {urgencyFilterData.map((data, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  key={index}
                                  closable
                                  onClose={() =>
                                    removeFilterLevel(
                                      "urgencyLevel",
                                      data.value
                                    )
                                  }
                                  className="mb-2"
                                >
                                  Urgency: <span>{data.label}</span>
                                </Tag>
                              </>
                            ))}
                            {hotlineFilterData.map((hotline, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  closable
                                  onClose={() =>
                                    removeFilterLevel(
                                      "urgencyHotline",
                                      hotline.value
                                    )
                                  }
                                  className="mb-2"
                                  key={index}
                                >
                                  Hotline: <span>{hotline.label} </span>
                                </Tag>
                              </>
                            ))}
                            {statusFilterData.map((status, index) => (
                              <>
                                <Tag
                                  visible={true}
                                  closable
                                  onClose={() =>
                                    removeFilterLevel(
                                      "urgencyStatus",
                                      status.value
                                    )
                                  }
                                  className="mb-2"
                                  key={index}
                                >
                                  Status: <span>{status.label} </span>
                                </Tag>
                              </>
                            ))}
                          </>
                        </Scrollbars>
                      </Col>
                      <Col span={24} className="search-box mt-3 px-3">
                        <Form form={form}>
                          <Form.Item name="search" id="search">
                            <Search
                              name="search"
                              id="search"
                              placeholder="Search"
                              onChange={(e) => onSearch(e.target.value)}
                              onSearch={onSearch}
                              autoComplete="off"
                            />
                          </Form.Item>
                        </Form>
                      </Col>
                      <Scrollbars
                        autoHeight
                        autoHeightMin={0}
                        autoHeightMax={450}
                        thumbMinSize={30}
                        universal={true}
                      >
                        <Col span={24} className="inbox-messages">
                          {conversations.length > 0 ? (
                            conversations.map((item, key) => {
                              return (
                                <div
                                  key={key}
                                  className={
                                    conversation.conversation_id
                                      ?.toString()
                                      .replace(/(<([^>]+)>)/gi, "") ===
                                    item.conversation_id
                                      ?.toString()
                                      .replace(/(<([^>]+)>)/gi, "")
                                      ? "msg-wrapper active"
                                      : "msg-wrapper"
                                  }
                                  onClick={() => onConversationClick(item)}
                                >
                                  <div className="d-flex justify-content-between">
                                    <p
                                      ellipsis="true"
                                      style={{ fontWeight: "bold" }}
                                      className="w-75 pr-2 mb-1"
                                      dangerouslySetInnerHTML={createMarkup(
                                        item.responder_conversation_name
                                      )}
                                    />
                                    <span className="small">
                                      {" "}
                                      {moment(item.created_on).isSame(
                                        moment(),
                                        "day"
                                      )
                                        ? moment(item.created_on)
                                            .utc()
                                            .local()
                                            .format("hh:mm")
                                        : moment(item.created_on)
                                            .utc()
                                            .local()
                                            .format("MM:DD:YY")}
                                    </span>
                                  </div>
                                  <div className="d-flex justify-content-between">
                                    <Paragraph
                                      ellipsis
                                      className="font-weight-normal mb-1 sm para_wrapper"
                                    >
                                      <span
                                        className="ellipsisText"
                                        dangerouslySetInnerHTML={createMarkup(
                                          item.hotline_name
                                        )}
                                      />
                                      &nbsp; |&nbsp;
                                      <span
                                        className="ellipsisText"
                                        dangerouslySetInnerHTML={createMarkup(
                                          item.conversation_id
                                        )}
                                      />
                                    </Paragraph>
                                    <span className="small">
                                      {" "}
                                      <Badge count={item.unread_messages_count} />
                                    </span>
                                  </div>
                                </div>
                              );
                            })
                          ) : (
                            <div className="msg-wrapper">No messages found</div>
                          )}
                        </Col>
                      </Scrollbars>
                    </Col>
                    {conversation ? (
                      <Messages
                        conversation={conversation}
                        urgencyLevel={urgencyLevel}
                        status={status}
                        onChangeUrgencyLevel={onChangeUrgencyLevel}
                        onChangeStatus={onChangeStatus}
                        searchString={searchString}
                        newState={newState}
                        socket={socket}
                        setCounter={setCounter}
                        data-test="Messages"
                        chatdiv={chatdiv}
                      />
                    ) : (
                      <Col
                        span={24}
                        lg={18}
                        className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
                      >
                        <Row className="justify-content-center my-3">
                          <img
                            src={NoHotlines}
                            alt="No Conversation Selected"
                            style={{ opacity: "0.8" }}
                          />
                        </Row>
                        <h5>No Conversation Selected</h5>
                        <small>Select a hotline to view</small>
                      </Col>
                    )}
                  </Row>
                </div>
              ) : (
                <Col
                  span={24}
                  lg={18}
                  className="chat-section no-selected bg-white justify-content-center align-items-center d-none d-lg-flex"
                >
                  <Row className="justify-content-center my-3">
                    <img
                      src={CreateHotline}
                      alt="No Hotlines Selected"
                      style={{ opacity: "0.8" }}
                    />
                  </Row>
                  <h5>
                    No conversations are initiated for the hotlines assigned to
                    you
                  </h5>
                  {/* <small>Select a hotline to view</small> */}
                </Col>
              )}
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default HotlineMessages;
