import React, { Component } from "react";
import { Modal, Button, Form, Col, Input, DatePicker } from "antd";
import FloatLabel from "../form/FloatLabels";
import moment from "moment";
import "../modal/modal.scss";
const { RangePicker } = DatePicker;

function onChange(dates, dateStrings) {
  console.log("From: ", dates[0], ", to: ", dates[1]);
  console.log("From: ", dateStrings[0], ", to: ", dateStrings[1]);
}

class DatetimeModal extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <>
        <Button type="primary" className="md mr-4" onClick={this.showModal}>
          Publish
        </Button>
        <Modal
          title="Publish Poll"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={520}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            className: "ant-btn-secondary",
            children: "Custom cancel",
          }}
          okText="Publish"
          cancelText="Cancel"
        >
          <Form className="input-md floating-label w-100">
            <Col span={24} className="gutter-row">
              <div className="mb-4 subtext-md font-weight-md6">
                Select Date and Time to <span className="pr-1">publish the Impact of Microaggressions</span>
                Poll
              </div>

              <FloatLabel label="Select date and time" value="">
                <Form.Item
                  name="date"
                  rules={[
                    {
                      required: true,
                      message: "Please select date",
                    },
                  ]}
                >
                  <RangePicker
                    ranges={{
                      Today: [moment(), moment()],
                      "This Month": [
                        moment().startOf("month"),
                        moment().endOf("month"),
                      ],
                    }}
                    showTime
                    format="YYYY/MM/DD HH:mm:ss"
                    onChange={onChange}
                  />
                </Form.Item>
              </FloatLabel>
            </Col>
          </Form>
        </Modal>
      </>
    );
  }
}

export default DatetimeModal;
